a, a:hover, a:visited {
    text-decoration: none;
}

body {
    font-family: 'Ubuntu', sans-serif;
}

.navimg {
    margin-top: 80px;
    height: 450px;
    background-image: url(images/front.jpg);
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center center;
}



.quote {
    color: white;
    text-align: center;
    border-bottom: 1px solid white;
    font-size: 40px !important;
    font-style: italic;
    padding-bottom: 15px;

}

.nav {
    position: sticky;
    top: 400px;
}

.text {
    width: 100%;
    margin-top: 29px;
    margin-left: auto;
    margin-right: auto;
    color: white;
    text-align: center;
}

.content {
    width: 100%;
    margin: 29px auto 320px;
    font-size: 24px !important;
    color: white;
    text-align: center;
}

.test {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

.test:hover {
    animation: highlight ease-in-out 1s;
    animation-fill-mode: forwards;
}

.postimg {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    width: 49%;
    margin-bottom: 24px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;


}

.image-margin{
    width: auto;
    max-width: 30%;
    max-height:300px;
    height: 45vh;
    margin-bottom: 24px;
}

.image-margin-small{
    width: auto;
    height: 400px
}

.lang-select {
    color:white;
    height:50px;
}
.css-hfutr2-MuiSvgIcon-root-MuiSelect-icon{
    color:white !important;
}

.header-image{
    height:300px;
    width: auto;
}

.white{
    color:white !important;
}

@keyframes highlight {
    to {
        transform: scale(1.08);
        opacity: .9;

    }
}


@media only screen and (max-width: 1500px) {
    .image-margin{
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 24px;
        width: 47%;
        height: auto;
        max-height: 70vh;
        max-width: 70vw;

    }

    .image-margin:nth-child(3){
        display:none;
    }

}

@media only screen and (max-width: 900px){
    .header-image{
        height:auto;
        max-height:300px;
        width: 95%;
        max-width: 400px;
        margin-right: auto;
        margin-left: auto;
    }
}
